import React from 'react';
import styled from 'styled-components';
import SideBar from './components/sidebar'
import Informatics from './components/Informatics'
import Links from './components/Links';

const ImageContainer = styled.div`
  display: flex;
  justify-content: center;
  height: 100vh;
  align-items: center;
`

const Image = styled.div`
  background-position: center;
  background-image: ${props => props.selectedPhoto.backgroundImage};
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
`


class Home extends React.Component {
  inputElement = React.createRef()
  constructor() {
    super()
    this.state ={
      currentImage: 1,
      items: [],
      currentItem: {text:'', key:''},
    }
  }


  handleInput = e => {
    const itemText = e.target.value
    const currentItem = { text: itemText, key: Date.now() }
    this.setState({
      currentItem,
    })
  }
  addItem = e => {
    e.preventDefault()
    const newItem = this.state.currentItem
    if (newItem.text !== '') {
      console.log(newItem)
      const items = [...this.state.items, newItem]
      this.setState({
        items: items,
        currentItem: { text: '', key: '' },
      });

      localStorage.clear();
      localStorage.setItem("items", JSON.stringify(items));
    }
  }

  deleteItem = key => {
    const filteredItems = this.state.items.filter(item => {
      return item.key !== key
    })
    this.setState({
      items: filteredItems,
    });
    localStorage.clear();
    localStorage.setItem("items", JSON.stringify(filteredItems));
  }

  rotateImage = () => {
    clearInterval(rotating);
    const rotating = setInterval(() => {
      const randomNumber = Math.ceil((Math.random()*33));
      this.setState({currentImage: randomNumber});
    }, 15000)
  }

  componentDidMount(){
    this.rotateImage();
    if(localStorage.getItem("items")) this.setState({items: JSON.parse(localStorage.getItem("items"))})
  }

  render(){
    console.log('in render',this.state.currentImage);
    console.log('localStorage', localStorage)
    console.log('state',this.state)
    const image = require(`./Images/${this.state.currentImage}.jpg`);
    if(!this.state.items) return(<p>'loading'</p>)
    return(
      <div>
        <Links/>
        <Informatics
          addItem={this.addItem}
          inputElement={this.inputElement}
          handleInput={this.handleInput}
          currentItem={this.state.currentItem}
          deleteItem={this.deleteItem}
          entries={this.state.items}
        />
        <ImageContainer>
          <Image
            className="fade"
            selectedPhoto={{
              backgroundImage: `url(${image})`
            }}
          />
        </ImageContainer>
      </div>
    )}

  }

  export default Home;
